<template>
  <div id="video-list">
    <blockquote v-if='!type'>
      <div class="videolist" v-for="(item, index) in videoList"  :key="index">
        <div class="topBox" v-if="currentindex !== index" @click="checkFn(item,index)">
          <div class="cover">
            <img :src="item.large_img_url" v-if="item.large_img_url" alt="图片">
          </div>
          <div class="coverbox">
            <div class="cover-bg">
              <div class="cover-title">{{item.title}}</div>
              <div class="cover-video-count">{{item.video_watch_count | setNumber}}次播放</div>
            </div>
            <div class="durtation">{{item.video_duration | setMinute}}</div>
          </div>
        </div>
        <div class="topBox videobox" ref="videosplay" v-if="(currentindex === index && showvideoDetails === true)">
        </div>
        <div class="bottomInfo" @click="toPage(item)">
          <div class="leftContent">
            <div class="leftIcon">
              <img :src="item.author_logo" v-if="item.author_logo" alt="">
            </div>
            <div class="rightInfo">{{$utils.splitStr(item.author)}}</div>
          </div>
          <div class="rightContent">
            <div class="rightInfo">
              <div class="play-icon">
                <img src="../../assets/img/video_detail_play.png" alt="播放">
              </div>
              <span>{{item.video_watch_count | setNumber}}</span>
            </div>
            <div class="rightInfo" @click.stop="setDigg(index)" :class="{active: item.is_fabulous === 1}">
              <div class="play-icon" >
                <img v-if="item.is_fabulous === 1" src="../../assets/img/video_detail_digg_active.png" alt="点赞">
                <img v-else src="../../assets/img/video_detail_digg.png" alt="点赞">
                <div class="text" :class="`diggStatus${item.digg_status}`">+1</div>
              </div>
              {{item.video_like_count | setNumber}}</div>
            <div class="rightInfo borderNone">
              <div class="play-icon">
                <img src="../../assets/img/video_detail_comment.png" alt="评论">
              </div>
              <span>{{item.comment_count | setNumber}}</span>
            </div>
          </div>
        </div>
      </div>
    </blockquote>
    <div class='listBox' v-if='type === 1'>
      <div class="recomd"  v-for="(item, index) in list"  :key="index" >
        <div class="leftInfo">
          <div class="topContent twoLine">{{item.title}}</div>
          <div class="leftBottom tip">
            <div class="media">{{item.author}}</div>
            <div class="play_num">{{item.video_watch_count | setNumber}}次播放</div>
          </div>
        </div>
        <div class="rightInfo">
          <div class="rightImage">
            <img :src="item.large_img_url" alt="">
            <div class="duration">
              <div class="videoImg">
                <img src="../../assets/img/video.png" alt="测试">
              </div>
              <span>{{item.video_duration | setMinute}}</span>
            </div>
          </div>
        </div>
      </div>
    </div>
    <list-loading :loading="loading"></list-loading>
    <list-no-data v-if="!type && (!videoList || videoList.length === 0)"></list-no-data>
  </div>
</template>
<script>
import {mapState, mapMutations} from 'vuex'
import listNoData from '@/components/common/list-no-data'
import ListLoading from '@/components/movie/common/list-loading'
export default {
  components: {
    listNoData,
    ListLoading
  },
  props: {
    list: {
      type: Array
    },
    type: {
      type: Number,
      default: 0
    },
    videoList: {
      type: Array
    },
    loading: {
      type: Number
    }
  },
  data () {
    return {
      digg_status: 0,
      currentindex: '',
      showvideoDetails: false,
      isPlay: false,
      topageid: ''
    }
  },
  computed: mapState({
    commonState: state => state.comment
  }),
  methods: {
    ...mapMutations([
      'comment_video_list',
      'comment_video_detail'
    ]),
    setDigg (index) {
      let tempData = this.videoList[index]
      if (tempData.is_fabulous === 0) {
        let data = {
          vid: tempData.id
        }
       
        this.$api.post('VIDEO_DIGG', data, res => {
          this.$emit('digg_status', {index, value: 1})
          this.$nextTick(() => {
            this.digg_status = 2
            this.$emit('digg_status', {index, value: 2})
            setTimeout(() => {
              this.$emit('digg_status', {index, value: 0})
            }, 400)
          })
          this.$emit('videoDigg', index)
        })
      }
    },
    checkFn (item, index) {
      if (this.$config.source_style >= 6) {
        if (this.$refs.videosplay && this.$refs.videosplay[0].children && this.$refs.videosplay[0].children[0]) {
          if (this.$utils.isIos()) {
            this.$refs.videosplay[0].removeChild(this.$refs.videosplay[0].children[0])
            this.$refs.videosplay[0].removeChild(this.$refs.videosplay[0].children[0])
          } else {
            this.$refs.videosplay[0].removeChild(this.$refs.videosplay[0].children[0])
          }
        }
        this.currentindex = index
        this.comment_video_detail(item)
        this.$store.commit('common_set_loading', 0)
        this.$api.get('VIDEO_DETAIL', {vid: item.id}, res => {
          this.details = res.data
          if (this.$config.source_style >= 6) {
            setTimeout(() => {
              this.showvideoDetails = true
              this.$nextTick(() => {
                let div = document.createElement('div')
                div.setAttribute('tt-videoid', this.details.video_id)
                div.setAttribute('tt-poster', this.details.large_img_url)
                div.className = 'tt-video-box'
                div.innerText = '视频加载中...'
                let oScript = document.createElement('script')
                oScript.type = 'text/javascript'
                oScript.src = 'https://s0.pstatp.com/tt_player/tt.player.js?v=20160723'
                for (let v of this.$refs.videosplay[0].children) {
                  if (v.localName === 'script') {
                    v.parentNode.removeChild(v)
                  }
                }
                this.$refs.videosplay[0].appendChild(div)
                this.$refs.videosplay[0].appendChild(oScript)
                if (this.videoTimer) {
                  clearInterval(this.videoTimer)
                  this.videoTimer = null
                }
                let that = this
                this.videoTimer = setInterval(() => {
                  if (this.videoE1) {
                    clearInterval((that.videoTimer))
                    this.videoTimer = null
                    if (this.isPlay) {
                      this.isPlay = false
                      this.videoE1.pause()
                    } else if (!this.isPlay) {
                    } else if (!this.isPlay) {
                      this.videoE1.play()
                    }
                    return false
                  }
                  // this.videoE1 = document.querySelector(`#${this.details.video_id}`)
                }, 100)
              })
            }, 600)
          } else {
            this.showvideoDetails = true
          }
        })
      } else {
        this.toPage(item, index)
      }
    },
    toPage (item, index) {
      if (this.$route.name === 'VideoDetail') {
        this.$router.replace('/VideoDetail/' + item.id)
      } else {
        this.$router.push('/VideoDetail/' + item.id)
      }
    }
  },
  mounted () {
  }
}
</script>

<style lang="scss" scoped>
#video-list{
  background: #fff;
  .videolist{
    height: 560px;
    .topBox{
      height: 450px;
      position: relative;
      box-sizing: border-box;
      .tt-video-box{
        height: 100%;
        padding: 0;
        video {
          display: block;
          height: 100%;
        }
      }
      .cover{
        width: 100%;
        height: 450px;
        overflow: hidden;
        img{
          width: 100%;
          height: 100%;
        }
      }
      .coverbox{
        position: absolute;
        left: 0;
        right:0;
        top: 0;
        bottom: 0;
        .cover-bg{
            padding: 0 30px 20px;
            // background: linear-gradient( rgba(0,0,0, .3), rgba(255, 255, 255, .3));
            background: url('../../assets/img/transparent_bg.png');
            background-size: 100% 100%;
            height: 100%;
        }
        //  padding: 0 30px;
        .cover-title{
          font-size: 36px;
          color: #fff;
          padding: 16px 0;
          line-height: 40px;
        }
        .cover-video-count{
          color: #fff;
          font-size: 20px;
        }
        .durtation{
          background: rgba(0, 0, 0, .5);
          position: absolute;
          color: #fff;
          font-size: 24px;
          line-height: 36px;
          height: 36px;
          padding:0 20px;
          border-radius: 20px;
          right: 22px;
          bottom: 22px;
        }
      }
    }
    .bottomInfo{
      display:flex;
      align-items:center;
      height: 110px;
      box-sizing: border-box;
      border-bottom: 1px solid #ddd;
      padding: 0 30px;
      .leftContent{
        display: flex;
        color: #999;
        font-size: 30px;
        align-items: center;
        .leftIcon{
          width:50px;
          height:50px;
          border-radius: 50%;
          margin-right: 20px;
          overflow: hidden;
          img{
            width: 100%;
            height: 100%;
          }
        }
      }
      .rightContent{
        display: flex;
        flex:1;
        font-size: 30px;
        height: 40px;
        min-width: 0;
        white-space: nowrap;
        justify-content: flex-end;
        .rightInfo {
          // padding: 0 20px;
          height: 40px;
          color: #999;
          display: flex;
          align-items: center;
          max-width: 130px;
          margin-right: 30px;
          justify-content: flex-end;
          &:last-child {
            margin-right: 0;
          }
          &.borderNone{
            border: none;
          }
          &.active {
            color: #f00;
          }
          .play-icon{
            width: 28px;
            margin-right: 10px;
            height: 33px;
            position: relative;
            .text {
              color: #f00;
              display: none;
              position: absolute;
              top: 50%;
              transform: translateY(-50%);
              -webkit-transform: translateY(-50%);
            }
            .diggStatus1 {
              display: block;
            }
            .diggStatus2 {
              display: block;
              animation: dropFn 400ms linear;
            }
            @keyframes dropFn {
              from {
                transform: translate(0, -80px)
              }
              to {
                transform: translate(0, 0)
              }
            }
            img{
              width: 100%;
              height: 100%;
            }
          };
          &:nth-child(2) .play-icon {
            width: 34px;
            height: 38px;
          }
          &:nth-child(3) .play-icon {
            width: 38px;
            height: 38px;
          }
        }
        
      }
     }
   }
   .recomd{
     display: flex;
     padding: 22px 30px 29px;
     border-bottom: 1px solid #eee;
     line-height: 50px;
     font-size: 34px;
     .leftInfo {
       flex: 1;
       min-width: 0;
       .tip{
         font-size: 24px;
         color: #959595;
       }
       .leftBottom{
         display: flex;
         &>div{
           min-width: 0;
         }
         &>div:first-child{
           margin-right: 20px;
         }
       }
     }
     .rightInfo{
       width: 224px;
       .rightImage{
         width: 224px;
          height: 145px;
          position: relative;
         img{
           width: 100%;
           height: 100%;
         }
         .duration{
          position: absolute;
          right: 10px;
          bottom: 10px;
          height: 26px;
          line-height: 26px;
          display: flex;
          align-items: center;
          padding: 0 13px;
          border-radius: 13px;
          background: rgba($color: #000000, $alpha: .3);
          color: #fff;
          font-size: 16px;
          white-space: nowrap;
          vertical-align: middle;
          span{
            vertical-align: middle;
            line-height: 26px;
            display: inline-block;
            font-size: 16px;
          }
          .videoImg{
            width: 26px;
            height: 26px;
            margin-right: 5px;
            display: flex;
            align-items: center;
            img{
              width: 100%;
              height: 100%;
            }
          }
        }
       }
       
     }
   }
 }
</style>



